<template>
  <div class="trialpreson flex-v page Detailedproject">
    <div class="headertitle flex">
      <el-breadcrumb separator-class="el-icon-arrow-right"
        class="my-breadcrumb ">
        <el-breadcrumb-item :to="{name:'TotalCheck'}">总检台</el-breadcrumb-item>
        <el-breadcrumb-item :to="{name:'conclusionsedit', query:{examUserId}}">检查结论</el-breadcrumb-item>
        <el-breadcrumb-item>项目明细</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="tabledata projectmain table-box"
      style="overflow-y:auto">
      <el-collapse v-model="activeNames">
        <template v-for="item in detailList">
          <el-collapse-item :name="item.sysGuid"
            :key="item.sysGuid"
            v-if="item.resultType==1">
            <template v-slot:title>
              <div class="icon">
                <div class="icon-in"></div>
              </div>
              {{item.pname}}
            </template>
            <el-table stripe
              ref="multipleTable"
              :data="item.smallProjectlist"
              tooltip-effect="dark"
              height='100%'
              header-cell-class-name='custom-table-cell'>
              <el-table-column type="index"
                width="50"
                label="序号">
              </el-table-column>
              <el-table-column align="center"
                label="小项名称"
                prop="pname"
                show-overflow-tooltip>
              </el-table-column>
              <el-table-column align="center"
                prop="checkValue"
                label="结果"
                width="120">
              </el-table-column>
              <el-table-column align="center"
                prop="projectSymbol"
                label="单位"
                show-overflow-tooltip>
              </el-table-column>
              <el-table-column align="center"
                prop="rule"
                label="参考值"
                show-overflow-tooltip>
              </el-table-column>
              <el-table-column align="center"
                prop="tishi"
                label="提示"
                show-overflow-tooltip>
                <template slot-scope="scope">
                  <i class="el-icon-bottom"
                    style="font-size:20px;color:#5AD4BB"
                    v-show="isNormal(scope.row)===-1"></i>
                  <i class="el-icon-top"
                    style="font-size:20px;color:#FE6F71"
                    v-show="isNormal(scope.row)===1"></i>
                </template>
              </el-table-column>
              <el-table-column align="center"
                label="体检时间"
                show-overflow-tooltip>
                <template slot-scope="scope">
                  <div>{{scope.row.updateDate | formatDate}}</div>
                </template>
              </el-table-column>
            </el-table>
          </el-collapse-item>
          <el-collapse-item :title="item.pname"
            :name="item.sysGuid"
            :key="item.sysGuid"
            v-if="item.resultType==2">
            <template v-slot:title>
              <div class="icon">
                <div class="icon-in"></div>
              </div>
              {{item.pname}}
            </template>
            <div class="itemlist">
              <div class="itemmain">
                <div class="flex">
                  <div>检查部位：</div>
                  <div class="flex-item">
                    {{handleCheckPart(item.smallProjectlist)}}
                  </div>
                </div>
                <div class="flex">
                  <div>检查所见：</div>
                  <div class="flex-item">
                    <div v-for="(list, i) in item.smallProjectlist"
                      :key="i">
                      <pre>{{list.checkConclusion}}</pre>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </el-collapse-item>
          <el-collapse-item :title="item.pname"
            :name="item.sysGuid"
            :key="item.sysGuid"
            v-if="item.resultType==3">
            <template v-slot:title>
              <div class="icon">
                <div class="icon-in"></div>
              </div>
              {{item.pname}}
            </template>
            <el-table stripe
              ref="multipleTable"
              :data="item.smallProjectlist"
              tooltip-effect="dark"
              height='100%'
              header-cell-class-name='custom-table-cell'>
              <el-table-column type="index"
                width="50"
                label="序号">
              </el-table-column>
              <el-table-column align="center"
                label="小项名称"
                prop="pname"
                show-overflow-tooltip>
              </el-table-column>
              <el-table-column align="center"
                prop="checkValue"
                label="结果"
                width="120">
              </el-table-column>
              <el-table-column align="center"
                label="体检时间"
                show-overflow-tooltip>
                <template slot-scope="scope">
                  <div>{{scope.row.updateDate | formatDate}}</div>
                </template>
              </el-table-column>
            </el-table>
          </el-collapse-item>
        </template>
      </el-collapse>
    </div>
  </div>
</template>
<script>
import successful from '@/components/Successfultip/SuccessfulTip'
export default {
  name: 'Detailedproject',
  components: {
    successful
  },
  data() {
    return {
      examUserId: '',
      detailList: [],
      activeNames: []
    }
  },
  created() {
    this.examUserId = this.$route.query.examUserId
    this.getData()
  },

  methods: {
    //获取人员基本信息
    getData() {
      let url = '/sys/project/getShowExam'
      let params = { sysGuid: this.examUserId }
      this.$post(url, params)
        .then(({ data }) => {
          this.detailList = data.bigProject.map((item, index) => {
            let bigProjectlist = {
              deptId: item.deptId,
              pname: item.pname,
              resultType: item.resultType,
              sysGuid: item.sysGuid
            }
            bigProjectlist.smallProjectlist = data.smallProject.filter(
              el => el.parentId === item.sysGuid
            )
            return bigProjectlist
          })
          this.activeNames = data.bigProject.map(item => item.sysGuid)
        })
        .catch(error => {
          this.$message.error(error)
        })
    },

    // 检测结果偏高/偏低
    isNormal({ rule, checkValue }) {
      let nCheckVal = Number(checkValue)
      if (checkValue === '' || isNaN(nCheckVal)) return 0
      let [bottom, top] = rule.split(' - ')
      let nBottom = bottom ? Number(bottom) : ''
      let nTop = top ? Number(top) : ''
      if (nBottom !== '' && nCheckVal < nBottom) {
        return -1
      }
      if (nTop !== '' && nCheckVal > nTop) {
        return 1
      }
      return 0
    },

    // 拼接检查部位
    handleCheckPart(list) {
      return list.map(t => t.checkPart).join('、')
    }
  }
}
</script>
<style lang="less" scoped>
@import '../../styles/FirstTrial/fristtrial.less';
.Detailedproject {
  .redcolor {
    color: red;
  }
  .headertitle {
    align-items: center;
    justify-content: space-between;
  }
  .projectmain {
    padding: 20px;
    /deep/.el-collapse-item:last-child .el-collapse-item__header {
      border-bottom: none;
    }
    /deep/.el-collapse {
      border-top: none;
    }
    /deep/.el-collapse-item__header {
      font-size: 16px;
      color: #111111;
      font-weight: 700;
    }
    .itemlist {
      .itemname {
        float: left;
        font-weight: 700;
        margin-right: 10px;
      }
    }
  }
  .icon {
    margin-right: 10px;
    padding: 6px 0 0 7px;
    width: 25px;
    height: 25px;
    border-radius: 8px;
    background: #0ed7b2;
    .icon-in {
      position: relative;
      width: 10px;
      height: 12px;
      border-width: 2px 0;
      border-style: solid;
      border-color: #fff;
      &:before {
        content: '';
        position: absolute;
        top: 3px;
        left: -1px;
        right: -2px;
        height: 2px;
        background: #fff;
      }
      &:after {
        content: '';
        position: absolute;
        top: 1px;
        left: 5px;
        width: 3px;
        height: 6px;
        background: #0ed7b2;
      }
    }
  }
}
</style>


